<template>
    <div id="Ueditor" style="margin:10px">
        <script id="editor" type="text/plain"></script>
        <br>
        <el-button @click="getUEContent"></el-button>
        <el-button @click="getUEText"></el-button>
    </div>
</template>
<script>
// ueditor
import '../../../static/ueditor/ueditor.config.js'
import '../../../static/ueditor/ueditor.all.min.js'
import '../../../static/ueditor/lang/zh-cn/zh-cn.js'
import '../../../static/ueditor/ueditor.parse.min.js'
export default {
    name:'Ueditor',
    data(){
        return{
            editor: null
        }
    },
    props: {
        defaultMsg: {
            type: String
        },
        config: {
            //ueditor.config.jsserverUrl
            serverUrl: 'http://172.16.254.49:83/File/UEditor',   //
            type: Object
        }
    },
    mounted() {
        const _this = this;
        this.editor = UE.getEditor('editor', this.config); // UE
        this.editor.addListener("ready", function () {
            _this.editor.setContent(_this.defaultMsg); // UE
        });
    },
    methods: {
        getUEContent() { // ,html<strong>11111</strong>
            console.log(this.editor.getContent())
            return this.editor.getContent()
        },
        getUEText(){//,
            console.log(this.editor.getPlainTxt())
            return this.editor.getPlainTxt()
        }
    },
    destroyed() {
        console.log('destroy')
        this.editor.destroy();
    }
}
</script>




